<template>
  <div id="getCash" class="scrollTouch" :class="{hasHeader: $utils.showNavBar()}">
    <HeaderNav v-if='$utils.showNavBar()'></HeaderNav>
    <div class="content">
      <div class="confirm">待确认收款</div>
      <div class="title posC">&yen; {{commonState.inviteVcoin}}<span>元</span></div>
      <div class="button posC" @click="toApp"></div>
      <div class="transfer posC">转账人：{{userInfo.nickname ? userInfo.nickname: '掘金宝'}}</div>
      <div class='isWx' v-if="$utils.isWx()">
        <div class="qrcode posC iconSize">
          <img v-if="$route.query.inviteCode" :src="`${$config.currentApi}/${$config.vsn}/wechat/qrcode?invitation_code=${$route.query.inviteCode}`" alt="">
        </div>
        <div class="longTap posC">长按识别二维码兑换<br>（服务号-我的-我的收入）</div>
        <!-- <div class="getButton"></div> -->

      </div>
      <div v-else class="isBrowser">
        <div class="downloadApp posC flex-row" @click="toApp">去APP确认提现</div>
      </div>
      <div class="exit posC" :class="{isWx: $utils.isWx()}">24小时内未领取，将退还给对方</div>
    </div>
    <ExitConfirm v-if="showConfirm" :carInfo='shareCarInfo' :config='config' @closeConfirm='closeConfirm'></ExitConfirm>
  </div>
</template>
<script>
import HeaderNav from '@/components/task/header-nav'
import ExitConfirm from '@/components/share/share_page2/exit_tip'
export default {
  components: {
    HeaderNav,
    ExitConfirm
  },
  computed: {
    commonState () {
      return this.$store.state.common
    }
  },
  data () {
    return {
      price: 0,
      userInfo: {},
      hasReward: true, // 默认进去有奖励
      showConfirm: false,
      shareCarInfo: {},
      config: {
        title: '你的现金还没有领取，逾期将收回确定要退出吗？',
        cancelText: '放弃领取',
        confirmText: '继续领钱'
      } // 组件文字配置
    }
  },
  // 在路由离开之前监听
  // beforeRouteLeave (to, from, next) {
  //   if (this.hasReward) {
  //     this.showConfirm = true
  //     next(false)
  //   } else {
  //     next()
  //   }
  // },
  methods: {
    getInviteUser () {
      if (this.$route.query.inviteCode) {
        this.$api.get('USER_INVITEINFO', {invitation_code: this.$route.query.inviteCode}, res => {
          if (res.data) {
            this.userInfo = res.data
          }
        })
      }
    },
    getHint () {
      this.$api.get('MESSAGE_HINT', {}, res => {
        this.$store.commit('common_set_vcoin', res.data.reward_coefficient)
      })
    },
    // 关闭弹窗
    closeConfirm ({action}) {
      if (action === 'confirm') {
        this.showConfirm = false
      } else if (action === 'cancel') {
        this.hasReward = false
        this.back()
      }
    },
    toApp () {
      this.$utils.openDownUrl()
    }
  },
  created () {
    this.getInviteUser()
    this.getHint()
  }
}
</script>
<style lang="scss" scoped>
#getCash{
  height: 100%;
  box-sizing: border-box;
  background:#b71406;
  .content {
    background: url('../../../assets/img/share/get_cash.png') no-repeat left -36px;
    background-size: 100%;
    height: 1205px;
    position: relative;
    .confirm {
      font-size: 60px;
      color: #F9CB5E;
      font-weight: 900;
      text-align: center;
      line-height: 84px;
      padding-top: 72px;
    }
    .button {
      width: 150px;
      height: 150px;
      top: 355px;
    }
    .transfer {
      top: 529px;
      font-size: 30px;
      opacity: .7;
      color: #fff;
    }
    .qrcode {
      position: absolute;
      width: 250px;
      height: 250px;
      top: 595px;
    }
    .longTap {
      top: 864px;
      line-height: 42px;
      text-align: center;
      font-size: 28px;
      color: #F9CB5E;
      font-weight: 600;
    }
  }
  .title {
    top: 240px;
    line-height: 92px;
    display: flex;
    justify-items: flex-end;
    justify-content: flex-end;
    align-items: flex-end;
    font-size: 66px;
    color: #F9CB5E;
    font-weight: 600;
    span {
      font-size: 36px;
      line-height: 80px;
    }
  }
  .exit {
    top: 730px;
    color: #fff;
    font-size: 24px;
    line-height: 33px;
    opacity: .7;
  }
  .exit.isWx {
    top: 985px;
  }
  .downloadApp {
    background-image: linear-gradient(to top, #FAD961, #F76B1C);
    border-radius: 10px;
    width: 526px;
    height: 85px;
    background-size: 100% 400%;
    background-repeat: 0 -200%;
    color: #fff;
    font-weight: 600;
    top: 615px;
  }
}
</style>



